
body{
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

html,body {
  min-height: 100%;

  font-family: poppins-extralight,poppins,sans-serif;
}

main{
	flex: 1 0 auto;
}

footer{
	background-color: #303030
}

.auto{
	margin: auto;
}

.navbar{
	.container{
		position: relative;
	}
	.navbar-brand{
		width: 150px;
		position: absolute;
		top: 10px;
		z-index: 1;
		left: 1rem;
	}
	a{
		color: #fff;
		font-size: 14px;
	}
}